import {useState, useEffect} from 'react'
import SockJS from 'sockjs-client'
import {Client} from '@stomp/stompjs'

function randomId() {
    const geraRandomInt = (min, max) => Math.floor(Math.random() * (max - min + 1) ) + min;
    const timestamp = new Date().toISOString().replace(/[^\d]/g, '')

    return( geraRandomInt(1000000, 9999999).toString() + timestamp )
}

export default function useStomp(props) {
    const [client] = useState(new Client({}))
    const [client_id] = useState(randomId())
    const [connected, setConnected] = useState(false)
    const [response, setResponse] = useState([])

    const {logger, request, flag} = props

    useEffect(() => {
        if(client.webSocketFactory === undefined){
            // console.log('configurando')
            client.configure({
                webSocketFactory: () => new SockJS('https://logbridge.herokuapp.com/sockjs'),
                onConnect: () => {
                    setConnected(true)
                    client.subscribe(`/response/${client_id}`, (message) => {
                        const dados = JSON.parse(message.body)
                        setResponse(dados.message)
                    })
                },
            })

            client.activate()
        }

    }, [client, client_id])

    useEffect(() => {
        if(connected){
            // console.log('enviando', request)
            client.publish({ destination: `/app/request/${logger}`, body: JSON.stringify({client_id, request}) })
        }

    }, [client, client_id, logger, connected, request, flag])

    return response;
}