import React from 'react'

import useStomp from '../useStomp'
import useClientId from '../useClientId'

import './ListaDados.css'

export default function ListaDados(props) {
    const {logger, transmissor} = props
    const client_id = useClientId()
    const request = `dados/${transmissor}`

    const listaDeDados = useStomp({logger, client_id, request})

    let cabecalho = {no : '', data_hora: ''}
    listaDeDados.map(registro => cabecalho = {...cabecalho, ...registro})
    cabecalho = Object.keys(cabecalho)

    if(listaDeDados.length === 0) return `Carregando dados do transmissor ${transmissor}...`

    return(
        <table className="lista_dados">

            <thead>
                <tr>
                    {cabecalho.map(campo => <th key={campo}>{campo}</th> )}
                </tr>
            </thead>

            <tbody>
            {listaDeDados.map(registro => (
                <tr className="lista_dados" key={registro.no + registro.data_hora}>
                    {cabecalho.map(campo => (
                        <td key={campo}>{
                            campo === 'data_hora' ?
                                new Date(registro[campo]).toLocaleString('pt-BR')
                            :
                                registro[campo]}
                        </td>
                    ))}
                </tr>
            ))}
            </tbody>
        </table>
    )
}