import React, {useState, useEffect} from 'react'

import './ListaTransmissores.css'
import useStomp from '../useStomp'

export default function ListaTransmissores(props) {
    const [flag, setFlag] = useState(0)
    const {logger} = props
    const request = 'ultima_transmissao'
    const listaDeTransmissores = useStomp({logger, request, flag})

    useEffect(() => {
        global.setTimeout(() => {
            setFlag(flag+1)
        }, 5000)

        return () => {
            global.clearTimeout()
        }
    })


    return(
        <ul className="lista_transmissores">
            {
                listaDeTransmissores.sort((a,b) => a.no > b.no).map(transmissor => (
                    <li 
                        className="lista_transmissores"
                        key={transmissor.no + transmissor.data_hora}
                        onClick={()=>props.onClick(transmissor)}
                    >
                        <div>{transmissor.no}</div>
                        <div>{new Date(transmissor.data_hora).toLocaleString('pt-BR')}</div>
                    </li>
                ))
            }
        </ul>
    )
}