import React, {useState} from 'react';

import {ListaTransmissores} from './ListaTransmissores/'
import {ListaDados} from './ListaDados'

import './App.css'
import useClientId from './useClientId';

function App(){
    const [logger] = useState('rflogger01')
    const [transmissor, setTransmissor] = useState(undefined)
    const clientId = useClientId()

    if(clientId === undefined) return null

    return (
        <div className="App">
            <div className="sidebar">
                <ListaTransmissores logger={logger} onClick={(transmissor)=>setTransmissor(transmissor.no)}/>
            </div>

            <div className="main">
            { transmissor !== undefined && logger !== undefined ?
                <ListaDados logger={logger} transmissor={transmissor}/>
            :
                null
            }
            </div>
        </div>
    )
}




export default App;